import * as React from "react";
import { Component } from "react";

import { connect } from "react-redux";
import keydown from "react-keydown";

import Intro from "components/Intro";
import FightStart from "components/FightStart";
import Game from "components/Game";
import Question from "components/Question";

import questions from "utils/questionsList";
import { updateState } from "reduxStore/actions";

import { StoreInterface } from "utils/types";
import { Dispatch } from "redux";

interface Props {
  state: string;
  dispatch: Dispatch;
}

interface State {
  questionID: number;
}

@keydown
class Index extends Component<Props, State> {
  componentWillReceiveProps(nextProps) {
    if (nextProps.state === "attack" || nextProps.state === "takeHit") {
      setTimeout(() => {
        this.setState(
          prevState => ({
            ...prevState,
            questionID: prevState.questionID + 1
          }),
          () => {
            this.props.dispatch(updateState("showQuestion"));
          }
        );
      }, 1500);
    }
  }

  state = {
    questionID: 0
  };

  render() {
    return (
      <div className="app">
        <Intro {...this.props} />
        <FightStart toShow={this.props.state === "startFight"} />
        <Question
          {...this.props}
          question={questions[this.state.questionID]}
          toShow={this.props.state === "showQuestion"}
        />
        <Game />
      </div>
    );
  }
}

function mapStateToProps(state: StoreInterface) {
  return { state: state.state };
}

export default connect(mapStateToProps)(Index);
